import axios from 'axios';

const url = 'http://localhost:7500/api/user';

export const login = async (email, password) => {
    try {
        const response = await axios.post(`${url}/auth`, JSON.stringify({
            email, password
        }),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
        return response;
    } catch (error) {
        throw error
    }
}

export const registerUser = async (firstName, lastName, gender, college, mobile, email, password) => {
    try {
        const response = await axios.post(`${url}/register`, JSON.stringify({
            firstName,
            lastName,
            gender,
            college,
            mobile,
            email,
            password
        }),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
        return response;
    } catch (error) {
        throw error
    }
}

export const saveImage = async (imageBase64, userName) => {
    try {
        const response = await axios.post(`${url}/savepicture`, JSON.stringify({
            imageBase64,
            userName
        }),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
        return response;
    } catch (error) {
        throw error
    }
}

export const getUserInfo = async (email, accessToken) => {
    try {
        const response = await axios.get(`${url}/${email}`, {
            headers: {
                'Content-Type': 'application/json',
                'Authorization': `Bearer ${accessToken}`
            }
        })

        return response;
    } catch (error) {
        throw error;
    }
}