import axios from 'axios';

const url = 'http://localhost:7500/api/admin';

export const login = async (email, password) => {
    try {
        const response = await axios.post(`${url}/auth`, JSON.stringify({
            email, password
        }),
            {
                headers: {
                    'Content-Type': 'application/json'
                }
            })
        return response;
    } catch (error) {
        throw error
    }
}