import { takeEvery, call, put, all, delay } from 'redux-saga/effects';
import { push } from 'connected-react-router';
import { setState } from '../actions/userActions';
import { login, registerUser, saveImage, getUserInfo } from '../../services/user';
import { login as adminAuth } from '../../services/admin'
import actions from '../actionTypes'


// Admin Login Saga
export function* adminLogin({ payload: { email, password } }) {
    try {
        yield put(setState({
            loading: true
        }))

        const response = yield call(adminAuth, email, password);

        if (response) {
            yield put(setState({
                loading: false,
                accessToken: response.data.accessToken,
                authenticated: true,
                errorOccurred: false,
                errorMessage: "",
                role: "admin"
            }))
            yield put(push('/admin/home'))
            sessionStorage.setItem('accessToken', response.data.accessToken)
            sessionStorage.setItem('authenticated', true)
            sessionStorage.setItem('role', 'admin')
        }
    } catch (error) {
        yield put(setState({
            errorOccurred: true,
            errorMessage: error.response.data.error.message,
            loading: false
        }))
    }
}

// User login Saga
export function* userLogin({ payload: { email, password } }) {
    try {
        yield put(setState({
            loading: true,
        }))

        const response = yield call(login, email, password);

        if (response) {
            const userInfoResponse = yield call(getUserInfo, email, response.data.accessToken);

            yield put(setState({
                loading: false,
                accessToken: response.data.accessToken,
                errorOccurred: false,
                errorMessage: "",
                authenticated: true,
                role: "user",
                firstName: userInfoResponse.data.userInfo.firstName,
                lastName: userInfoResponse.data.userInfo.lastName,
                registeredCourses: [...userInfoResponse.data.userInfo.registeredCourses]
            }))

            yield put(push('/user/home'))
            sessionStorage.setItem('accessToken', response.data.accessToken)
            sessionStorage.setItem('authenticated', true)
            sessionStorage.setItem('role', 'user')
        }
    } catch (error) {
        yield put(setState({
            errorOccurred: true,
            errorMessage: error.response.data.error.message,
            loading: false
        }))
    }
}

// Register Saga
export function* register({ payload: { firstName, lastName, gender, college, mobile, email, password } }) {
    try {
        yield put(setState({
            loading: true,
        }))

        const response = yield call(registerUser, firstName, lastName, gender, college,
            mobile, email, password);

        if (response) {
            yield put(setState({
                loading: false,
                errorOccurred: false,
                errorMessage: "",
                registrationSuccess: true,
                userName: `${firstName} ${lastName} ${email}`
            }))
            yield put(push('/user/picture'))
        }
    } catch (error) {
        yield put(setState({
            errorOccurred: true,
            errorMessage: error.response.data.error.message,
            loading: false,
            registrationSuccess: false
        }))
    }
}

export function* takePicture({ payload }) {
    yield put(setState({
        image: payload
    }))
}

export function* savePicture({ payload: { imageBase64, userName } }) {
    try {
        yield put(setState({
            loading: true,
        }))

        const response = yield call(saveImage, imageBase64, userName);

        if (response) {
            yield put(setState({
                loading: false,
                imageSaved: true
            }))

            yield delay(3000)

            yield put(push('/auth'))
            yield put(setState({
                imageSaved: false
            }))

        }
    } catch (error) {
        yield put(setState({
            errorOccurred: true,
            errorMessage: error.response.data.error.message,
            loading: false,
            imageSaved: false
        }))
    }
}

export function* logout() {
    yield put(setState({
        authenticated: false,
        accessToken: "",
        loading: false,
        errorOccurred: false,
        errorMessage: "",
        image: ""
    }))

    sessionStorage.setItem('accessToken', '');
    sessionStorage.setItem('authenticated', false)

    yield put(push('/auth'))
}


export default function* rootSaga() {
    yield all([
        takeEvery(actions.ADMIN_LOGIN, adminLogin),
        takeEvery(actions.USER_LOGIN, userLogin),
        takeEvery(actions.REGISTER, register),
        takeEvery(actions.TAKE_PICTURE, takePicture),
        takeEvery(actions.SAVE_PICTURE, savePicture),
        takeEvery(actions.LOGOUT, logout)
    ])
}